import { store, view } from '@risingstack/react-easy-state';


class Counter extends React.Component {
  counter = store({
    num: 0,
    increment: () => this.counter.num++,
    decrement: () => this.counter.num--,
  });

  componentDidMount() {
    this.autoUpdate();
  }
  
  // componentDidUpdate() {
  //   setTimeout(this.autoUpdate.bind(this), 360000000);
  // }

  state = { search: '' }
handleChange = event => {
/**
     * This is a simple implementation of a "debounce" function,
     * which will queue an expression to be called in 250ms and
     * cancel any pending queued expressions. This way we can
     * delay the call 250ms after the user has stoped typing.
     */
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      this.setState({
        search: event.target.value
      })
    }, 250);
  }

  autoUpdate() {
    const date = new Date();
    const hour = date.getHours();
    if (hour >= 9 && hour <= 17) {
      this.counter.increment();
    }
    if (hour <= 8 || hour >= 18) {
      this.counter.decrement();
    }
    // setTimeout(this.autoUpdate.bind(this), 3000);
  }

  render() {
    return (
      <div>
        <h1>Counter</h1>
        <p>{this.counter.num}</p>

        <button onClick={this.counter.increment}>+</button>
        <button onClick={this.counter.decrement}>-</button>
      </div>
    );
  }
}

export default view(Counter);